.heading {
  font-size: 24px;
}
.text-align-center{
  text-align: center;
}
.text-align-right{
  text-align: right;
}
.justify-flex-end{
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.justify-space-between{
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.Login-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* justify-content: center; */
  -ms-flex-align: center;
      align-items: center;
  /* text-align: center; */
  width: 100%;
  font-size: 14px;
  /* margin-top: 17vh; */
  /* height: 77vh; */
}

.Login-card{
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  /* flex: 1; */
  background-color: white;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-bottom: 8px;
  padding: 8px;
  border: 1px solid lightgray;
  -webkit-box-shadow: 0px 5px 5px -2px gray;
          box-shadow: 0px 5px 5px -2px gray; 
  /* flex-wrap: wrap; */
}
.Login-card-list{
  padding-left: 20px;
}
.Login-card-separator {
  margin-top: 4px;
  margin-bottom: 4px;
  border-bottom: 1px solid lightgray;
}

.Login-card-row{
  display: -ms-flexbox;
  display: flex;
  /* flex: 1; */
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  /* align-items: center; */
  /* justify-content: flex-start; */
  padding-bottom: 4px;
}
.show-border{
  border: 1px solid black;
}
.Login-card-col{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  /* align-items: center; */
  -ms-flex-direction: column;
      flex-direction: column;
}

.align-self-flex-start {
  -ms-flex-item-align: start;
      align-self: flex-start;
}
@media only screen and (min-width: 600px) {  
  .container {
    padding: 4px 8px 4px 8px;
  }
  .content {
    padding: 8px 8px 8px 8px;
  }
  .heading {
    font-size: 32px;
  }
  .Login-card {
    width: 60%;
  }
}


